<template>
    <div class="col-lg-12 control-section">
        <div class="col-md-12 py-0 mt-2 divGANTBUTTONS">
            <div class="row mt-0">
                <div class="col-6 p-0">
                    <div class="mr-2 d-inline-block"> 
                        <span class="border legend-Progress colorboxsquare"></span>{{ $t('Project') }}
                    </div>
                    <div class="mr-2 d-inline-block"> 
                        <span class="border legend-pending colorboxsquare"></span> {{$t('Tasks')}}
                    </div>
                    <div class="mr-2 d-inline-block"> 
                        <span class="border legend-upcoming colorboxsquare"></span> {{$t('ChildTasks')}}
                    </div>
                </div>
                <div class="col-6 float-right">
                    <div class="btn-group float-right">
                        <select v-model="TimeLineSettingData" v-on:change="ChangeChartTimeLine">
                            <option value="Week">Weekly</option>
                            <option value="Month">Monthly</option>
                            <option value="Day">Daily</option>
                            <option value="Year">Yearly</option>
                        </select>
                    </div>
                </div>
            </div>
        </div>
        <div>
            <ejs-gantt ref='gantt' :dataSource="LoadedGhanttData" id="GanttContainer" :taskFields="taskFields"
                :height="height" :timelineSettings="timelineSettings" :taskbarTemplate="'taskbarTemplate'">
                <template v-slot:taskbarTemplate="{ data }">
                    <div class="e-gantt-child-taskbar e-custom-moments" style="height:100%;border-radius:5px;">
                        <span class="e-task-label"
                            style="position:absolute;top:5px;font-size:12px;text-overflow:ellipsis;height:90%;overflow:hidden;">{{
                                data.ganttProperties.taskName }}</span>
                    </div>
                </template>
            </ejs-gantt>
        </div>
    </div>
</template>
<script>
import { registerLicense } from '@syncfusion/ej2-base';
registerLicense('ORg4AjUWIQA/Gnt2VlhiQlVPd11dXmJWd1p/THNYflR1fV9DaUwxOX1dQl9gSH9RdkVqWn5dcHBRRGQ=');
import { GanttComponent, Selection, DayMarkers } from "@syncfusion/ej2-vue-gantt";
import '@syncfusion/ej2-base/styles/material.css';
import '@syncfusion/ej2-gantt/styles/material.css';
import DataService from '../../services/DataService';
export default {
    components: {
        'ejs-gantt': GanttComponent
    },
    data: function () {
        return {
            TimeLineSettingData: 'Month',
            LoadedGhanttData: [],
            height: '450px',
            taskFields: {
                id: 'SerialNumber',
                name: 'name',
                description: 'description',
                startDate: 'start',
                endDate: 'end',
                duration: 'duration',
                milestone: 'Milestone'
                // progress: 'Progress',
                // child: 'subtasks'
            },
            timelineSettings: {},
            GetUserData: {}
        };
    },
    provide: {
        gantt: [DayMarkers, Selection]
    },
    async created() {
        var vm = this;
        vm.timelineSettings = {
            timelineViewMode: vm.TimeLineSettingData,
            timelineUnitSize: 150
        }
        vm.GetUserData = vm.$route.params.UserData;
        await vm.LoadGhanttChart();
    },
    methods: {
        async LoadGhanttChart() {
            var vm = this;
            var params = `userId=${vm.GetUserData.user_id}`;
            await DataService.LoadGhanttChart(params).then(response => {
                if (response.data.user != null) {
                    vm.LoadedGhanttData = response.data.user.tasks;
                    vm.LoadedGhanttData.forEach((item, index) => {
                        item.SerialNumber = index + 1;
                        item.start = new Date(item.start * 1000);
                        item.end = new Date(item.end * 1000);
                    })
                }
            }).catch(err => {
                console.log(err);
            })
        },
        ChangeChartTimeLine() {
            var vm = this;
            vm.timelineSettings = {
                timelineViewMode: vm.TimeLineSettingData,
                timelineUnitSize: 150
            };
        }
    }
}
</script>

<style>
.e-custom-moments {
    background-color: #7ab748;
    border: 1px solid #3f51b5;
}
</style>